<template>
    <div id="App">
        <div id="clipboard" v-if="clipOpenned" :style="styleClip">
            <div>
                <div id="header">
                    <div>
                        <label for="attendant">{{translate.TR_ATTENDANT}}</label>
                        <input type="text" id="attendant" spellcheck="false" v-model="attendant" :placeholder="translate.TR_P_ATTENDANT" readonly>
                    </div>

                    <div>
                        <label for="client">{{translate.TR_CLIENT}}</label>
                        <input type="text" id="client" spellcheck="false" v-model="client" :placeholder="translate.TR_P_CLIENT" readonly>
                    </div>

                    <div>
                        <label for="id">{{translate.TR_ID}}</label>
                        <input type="text" id="id" spellcheck="false" v-model="id" :placeholder="translate.TR_P_ID" readonly>
                    </div>

                    <div>
                        <label for="plate">Nummerplade</label>
                        <input type="text" id="plate" spellcheck="false" v-model="plate" :placeholder="translate.TR_PL_ID" readonly>
                    </div>

                    <div>
                        <label for="vehicle">{{translate.TR_VEHICLE}}</label>
                        <input type="text" id="vehicle" spellcheck="false" v-model="vehicle" :placeholder="translate.TR_P_VEHICLE" :readonly="!creating" v-on:change="verifyAdjust">
                    </div>

                    <div>
                        <label for="discount">Rabat</label>
                        <input type="number" min="0" max="100" step="1" v-model="discount" v-on:input="calculatePrice" :readonly="!creating">
                    </div>

                    <div>
                        <label for="price">{{translate.TR_PRICE}}</label>
                        <p type="text" id="price" >{{price}} kr.</p>
                    </div>
                </div>

                <div id="options">
                    <ul>
                        <li v-for="(item, index) in list_options" :key="index">
                            <div class="large">
                                <div class="checkbox" v-on:click="changeCheck(index)">
                                    <img v-if="item.checked" src="./assets/check.png" alt="">
                                </div>
                                <p>{{item.name}}</p>
                            </div>
                            <div class="small">
                                <p>{{translate.TR_QTD}}</p>
                                <input type="number" min="0" step="1" v-model="item.amount" v-on:input="calculatePrice" :readonly="!creating">
                            </div>
                            <p class="large item-price">{{item.price*item.amount}} kr.</p>
                        </li>
                    </ul>
                </div>

                <div class="end-price">
                    <label for="price">{{translate.TR_PRICE}}</label>
                    <p type="text" id="price" >{{price}} kr.</p>
                </div>

                <div class="finish" v-if="creating"><button v-on:click="saveClipboard">{{translate.TR_FINISH}}</button><button v-on:click="close">{{translate.TR_CANCEL}}</button></div>

                <div class="page" v-if="!creating">{{page+1}}/{{infoClient.length}}</div>
                <div class="next" v-if="!creating" v-on:click="nextPage(1)">&gt;</div>
                <div class="preview" v-if="!creating" v-on:click="nextPage(-1)">&lt;</div>
            </div>
            
        </div>    

        <div id="menu" v-if="menuOpenned" :style="styleMenu">
            <input type="text" v-model="search" :placeholder="translate.TR_SEARCH">
            <ul>
                <li v-on:click="newClipboard">{{translate.TR_NEW}}</li>
                <li
                    v-for="(item, index) in peoples.filter((item) => item.client.toLowerCase().includes(search.toLowerCase())||item.id_client == search)"
                    :key="index"
                    v-on:click="seePlayerClipboard(item.id_client)"
                >
                    {{item.id_client}} | {{item.client}}
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name: "App",
    data: function(){
        return({
            attendant   : "Rosbevaldo Claudio",
            client      : "Jubileu Claudio",
            id          : "15",
            vehicle     : "Zentorno",
            price       : "0",
            plate       : "None",
            discount    : "0",

            list_options: [
                {checked: false, name: "Ikke i stand til at starte", amount: 1, price: 4050},
            ],

            list_adjust: {},
            list_base : [],

            peoples: [
                {id_client: 1, client: "Jubileu Claudio"},
            ],

            infoClient : [],

            search : "",

            styleClip: {
                top: "50%",
                left: "50%",
                transform: "translate(-50%, -50%)",
            },
            styleMenu: {
                top: "50%",
                left: "50%",
                transform: "translate(-50%, -50%)",
            },

            translate:{
                TR_ATTENDANT    : "Atendente:",
                TR_CLIENT       : "Nome do cliente:",
                TR_ID           : "ID:",
                TR_VEHICLE      : "Veículo:",
                TR_QTD          : "Antal:",
                TR_PRICE        : "Preço Total:",
                TR_P_ATTENDANT  : "Digite o seu nome",
                TR_P_CLIENT     : "Digite o nome do cliente",
                TR_P_ID         : "Digite o id do client",
                TR_PL_ID         : "Indtast nummerplade",
                TR_P_VEHICLE    : "Digite o nome do Veículo",
                TR_NEW          : "Novo",
                TR_SEARCH       : "Pesquisar",
                TR_CANCEL       : "Cancelar",
                TR_FINISH       : "Finalizar"
            },

            clipOpenned: false,
            menuOpenned: false,
            NameResource: "clipboard",
            page : 0,
            creating : false,
        })
    },
    methods: {
        changeCheck: function(index){
            if(this.creating){
                this.list_options[index].checked = !this.list_options[index].checked;
                
                this.calculatePrice();
            }
        },

        calculatePrice: function(){
            this.price = 0;
            
            let calcDiscount = (this.discount / 100)

            for(let i = 0; i < this.list_options.length; i++){
                if(this.list_options[i].checked)
                    this.price = this.price + (this.list_options[i].price*this.list_options[i].amount);
            }

            if (calcDiscount !== 0) {
                this.price = Math.ceil(this.price - (this.price * calcDiscount))
            }
        },

        newClipboard: function(){
            this.client      = "";
            this.id          = "";
            this.vehicle     = "";
            this.price       = "0";
            this.discount    = "0";
            this.plate       = "Ikke udfyldt";

            for(let i = 0; i < this.list_options.length; i++){
                this.list_options[i].checked = false;
            }

            fetch(`https://${this.NameResource}/startCreating`, {
                method: 'POST',
                body: JSON.stringify({})
            });

            this.menuOpenned = false;
            this.clipOpenned = true;
            this.creating = true;
        },

        saveClipboard: function(){
            if(this.attendant.length > 0 && this.client.length > 0 && this.id.length > 0 && this.vehicle.length > 0){
                fetch(`https://${this.NameResource}/saveClipboard`, {
                    method: 'POST',
                    body: JSON.stringify({
                        attendant   : this.attendant,
                        client      : this.client,
                        id_client   : this.id,
                        vehicle     : this.vehicle,
                        price       : this.price,
                        discount    : this.discount,
                        plate       : this.plate
                    })
                }); 

               this.close();
            }
        },

        seePlayerClipboard: function(id_client){
            fetch(`https://${this.NameResource}/seePlayerClipboard`, {
                method: 'POST',
                body: JSON.stringify({
                    id_client   : id_client,
                })
            });
        },

        nextPage: function(value){
            this.page = this.page + value;

            if( this.page == this.infoClient.length)
                this.page = 0;
            else if(this.page < 0)
                this.page = this.infoClient.length-1;

            for(let i = 0; i < this.list_options.length; i ++){
                this.list_options[i].checked = this.infoClient[this.page][this.list_options[i].column] != 0 ? true : false;
                this.list_options[i].amount = this.infoClient[this.page][this.list_options[i].column];
            }

            this.attendant = this.infoClient[this.page].attendant;
            this.client = this.infoClient[this.page].client;
            this.id = this.infoClient[this.page].id_client;
            this.vehicle = this.infoClient[this.page].vehicle;
            this.price = this.infoClient[this.page].price;

            this.verifyAdjust();
        },

        verifyAdjust: function(){
            if(this.list_adjust[this.vehicle]){
                for(let j = 0; j < this.list_options.length; j++){
                    let found = false;
                    for(let i = 0; i < this.list_adjust[this.vehicle].length; i++){                    
                        if(this.list_options[j].name == this.list_adjust[this.vehicle][i].name){
                            this.list_options[j].price = this.list_adjust[this.vehicle][i].price;
                            found = true;
                        }
                    }

                    if(!found){
                        for(let i = 0; i < this.list_base.length; i++){                    
                            if(this.list_options[j].name == this.list_base[i].name){
                                this.list_options[j].price = this.list_base[i].price;
                            }
                        }
                    }
                }
            }
            else{
                for(let j = 0; j < this.list_options.length; j++){
                    for(let i = 0; i < this.list_base.length; i++){                    
                        if(this.list_options[j].name == this.list_base[i].name){
                            this.list_options[j].price = this.list_base[i].price;
                        }
                    }                    
                }                       
            }
        },

        close: function(){
            this.clipOpenned = false;
            this.menuOpenned = false;
            this.creating = false;

            fetch(`https://${this.NameResource}/close`, {
                method: 'POST',
                body: JSON.stringify({})
            }); 
        },

        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.configs){
                this.NameResource = lua.NameResource;
                this.translate = lua.translate;

                this.styleClip.left = lua.clipboard.positionX;
                this.styleClip.top = lua.clipboard.positionY;
                this.styleClip.transform = `scale(${lua.clipboard.size}) translate(-${lua.clipboard.positionX}, -${lua.clipboard.positionY})`;

                this.styleMenu.left = lua.menu.positionX;
                this.styleMenu.top = lua.menu.positionY;
                this.styleMenu.transform = `scale(${lua.menu.size}) translate(-${lua.menu.positionX}, -${lua.menu.positionY})`;

                this.list_adjust = lua.list_adjust;
            }

            if(lua.createCLipboard){
                this.attendant = lua.attendant;
                this.client = lua.client;
                this.id = lua.id_client;
                this.vehicle = lua.vehicle;
                this.price = "0";
                this.discount = "0";

                if (lua.plate) {
                    this.plate = lua.plate;
                } else {
                    this.plate = "Ikke udfyldt"
                }

                this.list_options = lua.list_options;

                this.menuOpenned = false;
                this.clipOpenned = true;
                this.creating = true;
            }

            if(lua.setClipboardData){
                if(!this.creating) return;

                this.attendant = lua.attendant;
                this.client = lua.client;
                this.id = lua.id_client;
                this.vehicle = lua.vehicle;
            }

            if(lua.seeClient){
                this.list_options = lua.list_options;

                for(let i = 0; i < lua.list_options.length; i ++){
                    this.list_options[i].checked = lua.resultClient[0][lua.list_options[i].column] != 0 ? true : false;
                    this.list_options[i].amount = lua.resultClient[0][lua.list_options[i].column];
                }

                this.attendant = lua.resultClient[0].attendant;
                this.client = lua.resultClient[0].client;
                this.id = lua.resultClient[0].id_client;
                this.vehicle = lua.resultClient[0].vehicle;
                this.price = lua.resultClient[0].price;

                this.page = 0;
                this.infoClient = lua.resultClient;

                this.clipOpenned = true;
                this.menuOpenned = false;
                this.creating = false;

                this.verifyAdjust();
            }
            
            if(lua.result){
                if(!this.creating){
                    this.attendant = lua.name;
                    this.list_options = lua.list_options;

                    for(let j = 0; j < this.list_options.length; j++){                 
                        this.list_base[j] = {name: this.list_options[j].name, price: this.list_options[j].price};
                    }    

                    this.peoples = lua.result

                    this.menuOpenned = true;
                }
                else{
                    this.clipOpenned = true;
                }
            }
        },
        keyPress: function(event){
            const key = event.key;
            
            if(key == "Escape"){
                fetch(`https://${this.NameResource}/close`, {
                    method: 'POST',
                    body: JSON.stringify({})
                }); 

                this.clipOpenned = false;
                this.menuOpenned = false;
            }
        },
    },
    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    }
}
</script>

